/* NavBar */

#navbar1 {
  height: 150px;
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0)
  );
  width: 100%;
}

/* NavBar image */

.logo-image {
  height: 250px;
  width: auto;
  border: solid rgb(161, 185, 190) 2px;
  border-radius: 50%;
  padding: 1%;
  transition: 2s;
  margin-top: 1%;
  margin-left: 1%;
}

.logo-image:hover {
  border: solid white 2px;
  transform: rotate(360deg);
}

/* NavBar links */

#nav-list-ul {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.link {
  width: 15%;
  height: 60%;
  text-align: center;
  padding-top: 3%;
  cursor: pointer;
  margin-left: 5%;
}

.link:hover {
  border-bottom: solid rgb(161, 185, 190) 2px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
}

.link p {
  color: rgb(224, 224, 224);
  text-transform: capitalize;
}

.git {
  margin-left: 25%;
  margin-top: 3%;
}

.git a {
  color: rgb(154, 182, 161);

  font-size: 35px;
}

.git a:hover {
  color: white;
}
